body {
  margin: 0;
}

.search-form {
  text-align: center;
}

.container {
  max-width: 100%;
  background-color: red;
  padding: 30px;
}

.loader {
  max-width: 100%;
  min-height: 60px;
  max-height: 60px;
  height: 5vh;
  z-index: 1;
  flex-basis: 100%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.loader.hidden {
  display: none;
}

.loader-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ball {
  position: absolute;
}

.loader :before, .loader :after {
  content: "";
  box-sizing: border-box;
  position: absolute;
}

@media screen and (min-width: 700px) {
  .loader {
    max-width: 33.33%;
    flex-basis: 33.33%;
  }
}

.default-03 .loader-container {
  width: 50px;
  height: 50px;
  animation: spin 2.5s cubic-bezier(.17, .72, .55, 1.66) infinite;
}

.default-03 .ball {
  width: 11%;
  height: 50%;
  transform-origin: bottom;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 50%;
  overflow: visible;
  transform: translate(-50%);
}

.default-03 .ball:nth-of-type(2) {
