.default-03 .ball:before {
  content: "";
  width: 100%;
  height: 56%;
  background-color: #00f;
  border-radius: 70px;
  position: absolute;
  top: 0;
  left: 0;
}

.default-03 .ball:after {
  content: "";
  width: 100%;
